import config from './'

const configureApp = () => {
  // replace app name in document dynamically from config
  const metas = document.getElementsByTagName('meta')
  const descriptionMeta = metas.namedItem('description')
  if (descriptionMeta) {
    descriptionMeta.content = config.appName
  }
  document.title = config.appName


  // create manifest for webapp
  const manifest = {
    "short_name": config.appName,
    "name": config.appName,
    "icons": [
      {
        "src": "weekly-planner/favicon.ico",
        "sizes": "64x64 32x32 24x24 16x16",
        "type": "image/x-icon"
      },
      {
        "src": "weekly-planner/logo192.png",
        "type": "image/png",
        "sizes": "192x192"
      },
      {
        "src": "weekly-planner/logo512.png",
        "type": "image/png",
        "sizes": "512x512"
      }
    ],
    "start_url": "./weekly-planner",
    "display": "standalone",
    "theme_color": "#000000",
    "background_color": "#ffffff"
  }

  const stringManifest = JSON.stringify(manifest)
  const blob = new Blob([stringManifest], { type: 'application/json' })
  const manifestURL = URL.createObjectURL(blob)
  // let reader = new FileReader()
  // reader.readAsDataURL(blob)
  // reader.onload = () => {
  //   document.getElementById('manifest-placeholder').setAttribute('href', reader.result);
  // }
  document.querySelector('#manifest-placeholder').setAttribute('href', manifestURL)
}

export default configureApp
